import { ChangeEvent } from 'react';
import { useSearchParams } from 'react-router-dom';

import CatalogPriceFilter from '../catalog-price-filter/catalog-price-filter';

import { QueryParam } from '../../const';

const CatalogFilter = () => {
  const [searchParams, setSearchParams] = useSearchParams();

  const handleFilterChange = (evt: ChangeEvent<HTMLInputElement>) => {
    const filterGroup = evt.target.name;
    const filterName = evt.target.id;
    const currentValues = searchParams.getAll(filterGroup);

    if (currentValues.includes(filterName)) {
      searchParams.delete(filterGroup);
      currentValues
        .filter((value) => value !== filterName)
        .forEach((value) => {
          searchParams.append(filterGroup, String(value));
        });
    } else {
      searchParams.append(filterGroup, String(filterName));
    }

    setSearchParams(searchParams);
  };

  const handleResetButtonClick = () => {
    const newParams = Array.from(searchParams.entries()).filter(
      ([key, value]) => key === QueryParam.Sort || key === QueryParam.Order,
    );

    setSearchParams(newParams);
  };

  return (
    <div className="catalog-filter">
      <form action="#">
        <h2 className="visually-hidden"></h2>
        <CatalogPriceFilter />
        <fieldset className="catalog-filter__block">
          <legend className="title title--h5"></legend>
          <div className="custom-checkbox catalog-filter__item">
            <label>
              <input
                type="checkbox"
                name={QueryParam.Category}
                id=""
                onChange={handleFilterChange}
                checked={searchParams.getAll('category').includes('')}
              />
              <span className="custom-checkbox__icon"></span>
              <span className="custom-checkbox__label"></span>
            </label>
          </div>
          <div className="custom-checkbox catalog-filter__item">
            <label>
              <input
                type="checkbox"
                name={QueryParam.Category}
                id=""
                onChange={handleFilterChange}
                checked={searchParams.getAll('category').includes('')}
                disabled={
                  searchParams.getAll('type').includes('') ||
                  searchParams.getAll('type').includes('')
                }
              />
              <span className="custom-checkbox__icon"></span>
              <span className="custom-checkbox__label"></span>
            </label>
          </div>
        </fieldset>
        <fieldset className="catalog-filter__block">
          <legend className="title title--h5"> </legend>
          <div className="custom-checkbox catalog-filter__item">
            <label>
              <input
                type="checkbox"
                name={QueryParam.Type}
                id=""
                onChange={handleFilterChange}
                checked={searchParams.getAll('type').includes('')}
              />
              <span className="custom-checkbox__icon"></span>
              <span className="custom-checkbox__label"></span>
            </label>
          </div>
          <div className="custom-checkbox catalog-filter__item">
            <label>
              <input
                type="checkbox"
                name={QueryParam.Type}
                id=""
                onChange={handleFilterChange}
                checked={searchParams.getAll('type').includes('')}
                disabled={
                  searchParams.getAll('category').includes('') &&
                  !searchParams.getAll('category').includes('')
                }
              />
              <span className="custom-checkbox__icon"></span>
              <span className="custom-checkbox__label"></span>
            </label>
          </div>
          <div className="custom-checkbox catalog-filter__item">
            <label>
              <input
                type="checkbox"
                name={QueryParam.Type}
                id=""
                onChange={handleFilterChange}
                checked={searchParams.getAll('type').includes('')}
                disabled={
                  searchParams.getAll('category').includes('') &&
                  !searchParams.getAll('category').includes('')
                }
              />
              <span className="custom-checkbox__icon"></span>
              <span className="custom-checkbox__label"></span>
            </label>
          </div>
          <div className="custom-checkbox catalog-filter__item">
            <label>
              <input
                type="checkbox"
                name={QueryParam.Type}
                id=""
                onChange={handleFilterChange}
                checked={searchParams.getAll('type').includes('')}
              />
              <span className="custom-checkbox__icon"></span>
              <span className="custom-checkbox__label"></span>
            </label>
          </div>
        </fieldset>
        <fieldset className="catalog-filter__block">
          <legend className="title title--h5"></legend>
          <div className="custom-checkbox catalog-filter__item">
            <label>
              <input
                type="checkbox"
                name={QueryParam.Level}
                id=""
                onChange={handleFilterChange}
                checked={searchParams.getAll('level').includes('')}
              />
              <span className="custom-checkbox__icon"></span>
              <span className="custom-checkbox__label"></span>
            </label>
          </div>
          <div className="custom-checkbox catalog-filter__item">
            <label>
              <input
                type="checkbox"
                name={QueryParam.Level}
                id=""
                onChange={handleFilterChange}
                checked={searchParams.getAll('level').includes('')}
              />
              <span className="custom-checkbox__icon"></span>
              <span className="custom-checkbox__label"></span>
            </label>
          </div>
          <div className="custom-checkbox catalog-filter__item">
            <label>
              <input
                type="checkbox"
                name={QueryParam.Level}
                id=""
                onChange={handleFilterChange}
                checked={searchParams.getAll('level').includes('')}
              />
              <span className="custom-checkbox__icon"></span>
              <span className="custom-checkbox__label"></span>
            </label>
          </div>
        </fieldset>
        <button
          className="btn catalog-filter__reset-btn"
          type="reset"
          onClick={handleResetButtonClick}
        >
           
        </button>
      </form>
    </div>
  );
};

export default CatalogFilter;
